<template>
   <div>
    <dia-log :is-show="isShowCheckDialog" @on-close="checkStatus">
        请检查你的支付状态!
        <div class="button" @click="checkStatus">
            支付成功
        </div>
        <div class="button" @click="checkStatus">
            支付失败
        </div>
    </dia-log>
    <dia-log :is-show="isShowSuccessDialog" @on-close="Success">
        购买成功!
    </dia-log>
    <dia-log :is-show="isShowFailDialog" @on-close="Success">
        购买失败!
    </dia-log>
   </div>
</template>

<script>
import DiaLog from 'components/base/dialog'
export default{
  components: {
    DiaLog
  },
  props: {
    isShowCheckDialog:{
        type:Boolean,
        default:false
    },
    orderId:{
        type:[String,Number]
    }
  },
  data () {
    return {
        isShowSuccessDialog:false,
        isShowFailDialog:false,
    }
  },
  methods: {
    checkStatus(){
    this.$http.get('http://localhost:3000/createOrder',{
        orderId: this.orderId
    }).then
      (res=>{
       this.isShowSuccessDialog = true
       this.$emit('on-close-check-dialog')
      },err=>{
        this.isShowFailDialog = true
        this.$emit('on-close-check-dialog')
      })
  },
  Success(){
     this.isShowSuccessDialog = false
     this.$router.push({path:'/orderList'})
  }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.button{
    margin-left: 8px;
}
</style>
