<script>
import VChooser from 'components/base/chooser'
import VSelection from 'components/base/selection'
import DiaLog from 'components/base/dialog'
import BankChooser from 'components/bankChooser'
import CheckOrder from 'components/checkOrder'
import _ from 'lodash'
export default{
  components: {
    VChooser,
    VSelection,
    DiaLog,
    BankChooser,
    CheckOrder
  },
  data () {
    return {
      isShow:false,
      buyType:[],
      versions:{},
      period:{},
      price:0,
      isShowCheckOrder:false,
      bankId:null,
      isShowErrDialog:false,
      orderId:undefined,
      buyTypes:[
        {
          label:'红色版',
          value:0
        },
        {
          label:'绿色版',
          value:1
        },
        {
          label:'紫色版',
          value:2
        }
      ],
      seleTypes:[
        {
          label:'上海',
          value:0
        },
        {
          label:'重庆',
          value:1
        },
        {
          label:'北京',
          value:2
        },
        {
          label:'深圳',
          value:2
        },
        {
          label:'广东',
          value:2
        },
        {
          label:'香港',
          value:2
        },
        {
          label:'澳门',
          value:2
        }
      ],
      periodList:[
        {
          label:'半年',
          value:0
        },
        {
          label:'一年',
          value:0
        },
        {
          label:'二年',
          value:0
        }
      ],
    }
  },
    methods: {
    hasParanChange(attr,val){
      this[attr] = val
      this.getPrice()
      console.log(this[attr])
    },
    getPrice(){
      let buyVersionsArray = _.map(this.versions,(item)=>{
        return item.value
      })
      let reqParams = {
        buyType:this.buyType.value,
        period:this.period.value,
        version:buyVersionsArray.join(',')
      }
      this.$http.get('http://localhost:3000/getPrice',reqParams).then
      (res=>{
        console.log(res);
        let data = res.body
        this.price = data.amount
      })
    },
    showPayDialog(){
      this.isShow = true
    },
    hideShowParDialog(){
      this.isShow = false
    },
    onChangeBanks(bankObj){
      this.bankId = bankObj.id
      console.log(this.bankId);
    },
    confirmBuy(){
       let buyVersionsArray = _.map(this.versions,(item)=>{
        return item.value
      })
      let reqParams = {
        buyType:this.buyType.value,
        period:this.period.value,
        version:buyVersionsArray.join(','),
        bankId:this.bankId
      }
      this.$http.get('http://localhost:3000/createOrder',reqParams).then
      (res=>{
        this.orderId = res.body.orderId
        this.isShowCheckOrder = true
        this.isShow=false
      },err=>{
        this.isShowBuyDialog = false
        this.isShowErrDialog = true
      })
    },
    hideErrDialog(){
       this.isShowErrDialog = false
    },
    hideCheckOrder(){
       this.isShowCheckOrder = false
    }
  },
  mounted () {
      this.buyType = this.buyTypes[0]
      this.versions = this.seleTypes[0]
      this.period = this.periodList[0]
      this.getPrice()
  }
}
</script>

